}
#ryqk .is-left :hover{
    color: #029348 !important;
}
#ryqk .el-card{
    padding: 40px 0 40px 0;
}
#ryqk .shaixaun{
    padding: 82px 0 32px 0;
}
.el-tabs__content{
    overflow:inherit;
    padding-bottom: 20px;
}
.el-card{
    transition: 0s;
}
